
$circus-tan: #fff9f0;
$circus-black: #050505;
$circus-grey: #F2F2F2;
$circus-orange: #C03F2A;

$max-page-width: 60rem;

$typescale: (
  1: 3.052rem,
  2: 2.441rem,
  3: 1.953rem,
  4: 1.563rem,
  5: 1.25rem,
  6: 1rem,
  7: 0.8rem,
  8: 0.64rem,
  9: 0.512rem,
);


/*
 * 
 * Hamburger menu mods
 *  */
 
 /* Only include spin */
$hamburger-types: ('spin');

$hamburger-layer-width         : 30px;
$hamburger-layer-height        : 3px;
$hamburger-layer-spacing       : 4.5px;

/* Override color */
$hamburger-layer-color: white;